import React from 'react';
import './PlayerControl.css'
import { faPlay,faVolumeUp,faVolumeDown } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const PlayerControl = () => {
    return (
        <div className="player-control">
            <div className="player-control__play">
                <FontAwesomeIcon icon={faPlay} size="3x" />
            </div>
            <div className="player-control__volume">
                <div className='volume-down'>
                    <FontAwesomeIcon icon={faVolumeDown} size="2x" />
                </div>
                <input type="range" min="0.0" max="1.0" step="0.1" value="0.8"/>
                <div className='volume-up'>
                    <FontAwesomeIcon icon={faVolumeUp} size="2x" />
                </div>
            </div>
            <div className="player-control__music-time">00:00:00</div>
            <div className="player-control__track-name">NIKTO NE NUZHEN</div>
        </div>
    );
};

export default PlayerControl;