import React from 'react';
import './App.css'
import Header from "./Components/Header/Header";
import PlayerContent from "./Components/PlayerContent/PlayerContent";
import PlayerControl from "./Components/PlayerControl/PlayerControl";
import PlayList from "./Components/PlayList/PlayList";

class App extends React.Component {
    state = {visiblePlaylist:true}
    onToggleVisible = () => {
        this.setState({visiblePlaylist:!this.state.visiblePlaylist})
    }
    render() {
        return (
            <>
                <div className='app-wrapper'>
                    <Header visiblePlaylist={this.state.visiblePlaylist} onToggleVisible={this.onToggleVisible}/>
                    <PlayerContent/>
                    <PlayerControl/>
                    <PlayList visiblePlaylist={this.state.visiblePlaylist} onToggleVisible={this.onToggleVisible}/>
                </div>
            </>
        );
    }
}

export default App;
