import React from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faHeadphones} from "@fortawesome/free-solid-svg-icons";

const Music = () => {
    let testUrl = 'https://images.unsplash.com/photo-1606787733258-1cc187118992?ixid=MXwxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80';
    return (
        <div className="music">
            <div className="music__photo" style={{backgroundImage:`url(${testUrl})`}}></div>
            <div className="music__description">
                <div className="music__group-name">Kizaru</div>
                <div className="music__track-name">NIKTO NE NUZHEN</div>
            </div>
            <div className="viewers">
                <FontAwesomeIcon icon={faHeadphones} size="1x" /><span>&nbsp;7954</span>
            </div>
        </div>
    );
};

export default Music;