import React from 'react';
import './PlayerContent.css'

const PlayerContent = () => {
    let testUrl = 'https://images.unsplash.com/photo-1606787733258-1cc187118992?ixid=MXwxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80';
    return (
        <div className="player-content">
            <div className="photo-track" style={{backgroundImage:`url(${testUrl})`}}></div>
            <div className="description">
                <div className="description__group-name">Kizaru</div>
                <div className="description__track-name">NIKTO NE NUZHEN</div>
            </div>
        </div>
    );
};

export default PlayerContent;