import React from 'react';
import './Header.css'

const Header = (props) => {
    let onClickButton = (e) => {
        props.onToggleVisible();
    }
    return (
        <header className="header">
            <div className="logo">
                Slavik Player
            </div>
            <div className="menu-burger">
                <button className='btn-menu' onClick={(e) => onClickButton(e)}>
                    <div className='burger__piece'></div>
                    <div className='burger__piece'></div>
                    <div className='burger__piece'></div>
                </button>
            </div>
        </header>
    );
};

export default Header;